"use client";
import React, { useState, useEffect } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";

// Û±. ÙˆØ§Ø±Ø¯ Ú©Ø±Ø¯Ù† Ø§Ù„Ú¯ÙˆÙ‡Ø§ÛŒ Ù…ÙˆØ±Ø¯ Ù†ÛŒØ§Ø²
import {
  YadakchiAdminLayout,
  ListPageLayout, // <-- Ø§Ù„Ú¯ÙˆÛŒ Ø³Ø§Ø¯Ù‡ Ø´Ø¯Ù‡
  Label,
  type ColumnDefinition,
} from "@my-ds/design-system";

import { getShops, type Shop } from "../../services/shopService";

export default function HomePage() {
  const [shops, setShops] = useState<Shop[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const pathname = usePathname();
  const renderLink = (href: string, children: React.ReactNode) => <Link href={href}>{children}</Link>;

  useEffect(() => {
    const fetchShops = async () => {
      try {
        setIsLoading(true);
        const response = await getShops(1, 30);
        setShops(response.items);
      } catch (err) {
        setError(err instanceof Error ? err.message : "ÛŒÚ© Ø®Ø·Ø§ÛŒ Ù†Ø§Ø´Ù†Ø§Ø®ØªÙ‡ Ø±Ø® Ø¯Ø§Ø¯.");
      } finally {
        setIsLoading(false);
      }
    };
    fetchShops();
  }, []);

  const shopColumns: ColumnDefinition<Shop>[] = [
    { accessorKey: 'shopTitle', header: 'Ù†Ø§Ù… ÙØ±ÙˆØ´Ú¯Ø§Ù‡' },
    { accessorKey: 'name', header: 'Ù†Ø§Ù… ÙØ±ÙˆØ´Ù†Ø¯Ù‡', cell: (shop) => `${shop.name} ${shop.lastname}` },
    { accessorKey: 'registrationStatus', header: 'ÙˆØ¶Ø¹ÛŒØª', cell: (shop) => <Label text={shop.registrationStatus} size="sm" variant={shop.registrationStatus === 'Confirmed' ? 'primary' : 'error'} /> },
  ];
  
  // Û². Ø¢Ù…Ø§Ø¯Ù‡â€ŒØ³Ø§Ø²ÛŒ props Ø¨Ø±Ø§ÛŒ ListPageLayout (Ø³Ø§Ø¯Ù‡ Ø´Ø¯Ù‡)
  const listPageProps = {
    tableProps: {
      columns: shopColumns,
      data: shops,
      emptyStateMessage: "Ù‡ÛŒÚ† ÙØ±ÙˆØ´Ú¯Ø§Ù‡ÛŒ ÛŒØ§ÙØª Ù†Ø´Ø¯.",
    },
    isLoading: isLoading,
    error: error,
  };

  return (
    <YadakchiAdminLayout
      currentPath={pathname}
      renderLink={renderLink}
    >
      {/* Û³. Ø§Ø³ØªÙØ§Ø¯Ù‡ Ø§Ø² Ø§Ù„Ú¯ÙˆÛŒ Ø³Ø§Ø¯Ù‡ Ø´Ø¯Ù‡ */}
      <ListPageLayout {...listPageProps} />
    </YadakchiAdminLayout>
  );
}